"use client";

import { useRouter } from "next/navigation";
import { Activity, Factory, Cpu, Loader2, ArrowRight, LogOut } from "lucide-react";
import React, { useState, useEffect } from "react";

/* ---------- icon map ---------- */
const iconMap: any = {
  live: Activity,
  store: Factory,
  tasks: Cpu,
};

/* ---------- glassmorphic card component ---------- */
function DashboardCard({ title, gradient, onClick, icon: Icon, isLoading }: any) {
  return (
    <button
      onClick={onClick}
      disabled={isLoading}
      className={`
        relative overflow-hidden
        cursor-pointer select-none
        rounded-2xl
        p-8 sm:p-10
        bg-white/10 backdrop-blur-md
        border border-white/20
        shadow-xl
        hover:shadow-2xl hover:translate-y-[-4px] active:translate-y-[0px]
        transition-all duration-300 ease-out
        flex flex-col items-center justify-center
        w-full max-w-sm sm:w-80 h-44 sm:h-52
        disabled:opacity-50 disabled:cursor-not-allowed
        focus:outline-none focus:ring-2 focus:ring-white/30
        group
      `}
    >
      {/* Gradient overlay on hover */}
      <div className={`absolute inset-0 bg-gradient-to-br ${gradient} opacity-80 group-hover:opacity-100 transition-opacity duration-300`} />
      
      {/* Floating icon with subtle glow */}
      <div className="relative z-10 mb-4 transform group-hover:scale-110 transition-transform duration-300">
        <div className="absolute inset-0 bg-white/30 blur-xl rounded-full scale-150 opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
        <Icon className="w-12 h-12 sm:w-14 sm:h-14 text-white/90 drop-shadow-lg relative z-10" />
      </div>
      
      {/* Title with subtle text shadow */}
      <h2 className="relative z-10 text-lg sm:text-xl font-semibold text-white/95 text-center drop-shadow-md tracking-wide">
        {title}
      </h2>

      {/* Hover indicator arrow */}
      <ArrowRight className="absolute bottom-4 right-4 w-5 h-5 text-white/70 opacity-0 group-hover:opacity-100 group-hover:translate-x-1 transition-all duration-300" />

      {/* Loading overlay */}
      {isLoading && (
        <div className="absolute inset-0 bg-black/20 backdrop-blur-sm flex items-center justify-center z-20">
          <Loader2 className="w-8 h-8 text-white animate-spin" />
        </div>
      )}
    </button>
  );
}

/* ---------- hook: read role from JWT ---------- */
function useRole() {
  const [role, setRole] = useState<string>("");
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const token = localStorage.getItem("access_token");
    if (!token) {
      setIsLoading(false);
      return;
    }
    try {
      const payload = JSON.parse(atob(token.split(".")[1]));
      setRole(payload.role ?? "");
    } catch {
      setRole("");
    } finally {
      setIsLoading(false);
    }
  }, []);

  return { role, isLoading };
}

/* ---------- mobile navigation bar ---------- */
function MobileNav({ onLogout }: { onLogout: () => void }) {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  return (
    <div className="md:hidden fixed top-0 left-0 right-0 z-50 bg-white/90 backdrop-blur-md shadow-sm">
      <div className="flex justify-between items-center p-4">
        <span className="text-lg font-bold text-gray-800">BGI System</span>
        <button
          onClick={() => setIsMenuOpen(!isMenuOpen)}
          className="p-2 rounded-md text-gray-600 hover:bg-gray-100 transition-colors"
        >
          <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
          </svg>
        </button>
      </div>
      {isMenuOpen && (
        <div className="border-t border-gray-200 bg-white/95 backdrop-blur p-4 space-y-2">
          <button
            onClick={onLogout}
            className="w-full flex items-center justify-center gap-2 px-4 py-3 rounded-lg text-red-600 hover:bg-red-50 font-medium transition-colors"
          >
            <LogOut className="w-4 h-4" />
            Logout
          </button>
        </div>
      )}
    </div>
  );
}

/* ---------- main dashboard ---------- */
export default function HomePage() {
  const router = useRouter();
  const { role, isLoading } = useRole();
  const [isLoggingOut, setIsLoggingOut] = useState(false);

  const handleLogout = async () => {
    setIsLoggingOut(true);
    try {
      const token = localStorage.getItem("access_token");
      if (token) {
        await fetch("http://127.0.0.1:8000/auth/logout", {
          method: "POST",
          headers: {
            Authorization: `Bearer ${token}`,
            "Content-Type": "application/json",
          },
        });
      }
    } catch (err) {
      console.error("Logout API error:", err);
    } finally {
      localStorage.removeItem("access_token");
      router.replace("/login");
    }
  };

  /* ---------- buttons that exist for each role ---------- */
  const buttons = React.useMemo(() => {
    if (isLoading) return [];

    const all = [
      {
        name: "Live Terminals",
        gradient: "from-emerald-400/60 to-emerald-600/60",
        route: "/live",
        icon: iconMap.live,
      },
      {
        name: "Task Update",
        gradient: "from-rose-400/60 to-rose-600/60",
        route: "/task-manager",
        icon: iconMap.tasks,
      },
    ];

    /* super-admin extra cards */
    if (role === "superadmin") {
      all.push({
        name: "Inventory Terminals",
        gradient: "from-blue-500/60 to-blue-700/60",
        route: "/inventory",
        icon: iconMap.store,
      });
    }

    return all;
  }, [role, isLoading]);

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-blue-50 via-indigo-50 to-purple-50">
        <div className="relative">
          <Loader2 className="w-12 h-12 text-blue-600 animate-spin" />
          <div className="absolute inset-0 w-12 h-12 rounded-full border-2 border-blue-200 animate-ping" />
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen flex flex-col items-center bg-gradient-to-br from-blue-50 via-indigo-50 to-purple-50">
      {/* Mobile Navigation */}
      <MobileNav onLogout={handleLogout} />

      {/* Header */}
      <header className="hidden md:flex w-full bg-white/80 backdrop-blur-md shadow-md flex flex-col items-center py-8">
        <div className="flex flex-col items-center space-y-4">
          <div className="relative">
            <div className="absolute inset-0 bg-blue-500/20 blur-3xl rounded-full w-48 h-48" />
            <img
              src="/BGI-logo.jpg"
              alt="BGI Logo"
              className="h-24 w-auto object-contain relative z-10"
            />
          </div>
          <h1 className="text-3xl md:text-4xl font-bold bg-gradient-to-r from-gray-800 to-gray-600 bg-clip-text text-transparent tracking-wide">
            BGI INVENTORY SYSTEM
          </h1>
          <p className="text-sm text-gray-500 font-medium">Professional Asset Management Platform</p>
        </div>
      </header>

      {/* Mobile Header */}
      <div className="md:hidden w-full bg-white/80 backdrop-blur-md shadow-sm flex flex-col items-center py-5 pt-20">
        <div className="flex flex-col items-center space-y-3">
          <img
            src="/BGI-logo.jpg"
            alt="BGI Logo"
            className="h-16 w-auto object-contain"
          />
          <h1 className="text-xl font-bold text-gray-800 text-center">
            BGI INVENTORY SYSTEM
          </h1>
        </div>
      </div>

      {/* Main Content */}
      <main className="flex-1 flex flex-col items-center w-full p-4 sm:p-6 md:p-10 lg:p-12">
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6 sm:gap-8 md:gap-10 mt-6 md:mt-10 w-full max-w-6xl">
          {buttons.map((btn) => (
            <DashboardCard
              key={btn.name}
              title={btn.name}
              gradient={btn.gradient}
              onClick={() => router.push(btn.route)}
              icon={btn.icon}
              isLoading={isLoggingOut}
            />
          ))}
        </div>
      </main>

      {/* Footer */}
      <footer className="w-full py-6 bg-white/80 backdrop-blur-md border-t border-gray-200 text-center">
        <p className="text-gray-500 text-sm font-medium">
          © {new Date().getFullYear()} BGI • All Rights Reserved • Professional Asset Management
        </p>
      </footer>
    </div>
  );
}